@import "color";

.curriculum-list-wrap { padding: 1rem .5rem; border: 1px solid #e8e8f8; border-radius: .375rem; margin-bottom: 1.5rem;
    .tlt {font-size: 1.2rem; margin-bottom: 1.25rem;}
    .curriculum-list {
        .curriculum-item {padding: .5rem 0; cursor: pointer; display: flex; align-items: center; justify-content: space-between; margin-bottom: 0.5rem;
            &.active {font-weight: 700;}
        }
    }
}
.test-wrap {border: 1px solid #e8e8f8; padding: 1rem; border-radius: .375rem; margin-bottom: 1.25rem;
    .tlt {font-size: 1.2rem; margin-bottom: 1.25rem;}
    .question-wrap {
        .tlt {font-size: 1rem; margin-bottom: 0.5rem;}
    }
    .option-wrap {
        .radio-wrap, .check-wrap{
            &:not(:last-child) {margin-right: .5rem;}
        }
    }
}

.result-wrap {border: 1px solid #e8e8f8; padding: 1rem; border-radius: .375rem; margin-bottom: 1.25rem; display: flex; justify-content: space-between; align-items: center;
    p {font-weight: 700; font-size: 1.2rem;}
}